body {
  margin: 60px auto;
  font-family: 'Roboto Mono', monospace;
  height: auto;

  padding-left: 1em;
  text-align: center;
  background-color: #ffffff;
}
nav ul,
footer ul {
  font-family: 'Roboto Mono', monospace;
  list-style: none;
  font-weight: bold;
}

.navtitle {
  position: fixed;
  left: 1em;

  padding: 0.2em;
  font-size: 2em;
  font-weight: 900;
  font-family: "DM Serif Text", serif;
  color: #1f271b;
}
nav ul li,
footer ul li {
  display: inline;
  margin-right: 20px;
  margin-bottom: 0px;
}
nav a {
  color: #1f271b;
}
a {
  text-decoration: none;
  color: #1f271b;
}
a:hover {
  text-decoration: underline;
}
h1 {
  font-size: 3em;
  font-family: "DM Serif Text", serif;
                   margin-bottom: 0em;                             
  font-weight: 900;
  color: #1f271b;
}
h2 {
  font-size: 1.5em;
  font-weight: 900;
  font-family: "DM Serif Text", serif;
                                                
  color: #1f271b;
}
h3 {
  font-size: 1em;
  color: #1f271b;
}
.poster {
  width: 60%;
}
.container {
  padding-top: 1em;
}

.skills{
  width: 50%;
  color:rgb(19, 100, 53);
  text-align: left;
  margin-left: auto;
  margin-right: auto;
}

.values {
  display: flex;
  width: 80%;
  flex-direction: row;
  margin: 1em auto;
}

.values div {
  margin: 1em auto;
  width: 10em;
}

p {
  font-size: 1.25em;
  line-height: 1em;
  color: #333;
}
nav {
  background-color: #ffffff;
  padding: 1em 0em 1em 0em;
z-index: 99;
  position: fixed;
  left: 0px;
  width: 100%;
  top: 0px;
}

.project_img{
  width: 400px;
}

footer {
  font-size: 0.8em;
  position: fixed;
  width: 100%;
  bottom: 0px;
  color: #2e4d3f;
  left: 0px;
  background-color: #ffffff;
}

ul.posts {
  margin: 20px auto 40px;
  font-size: 1.5em;
}

ul.posts li {
  list-style: none;
}
.myface {
  border-radius: 250px;
  margin: 1em;
}

.logo {
  vertical-align: middle;
  margin-left: 0.5em;
}
.blurb {
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}

.left{
  text-align: left;
}

.githublink {
  position: absolute;
  right: 0px;
  top: 1.5em;
}

.bestbuttonever {
  color: white;
  background-color: #1f271b;
  padding: 1em;
  margin: 1em auto;
  border: none;
  padding-left: 2em;
  padding-right: 2em;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
  font-family: "DM Serif Text", serif;
}

.exp{
    width: 50%;
  
}

.linkedinlink {
  position: absolute;
  right: 3em;
  top: 1.5em;
}


.flex-container {
  display: flex;
  flex-wrap: wrap;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}

.flex-container > div {
  
  margin: 10px;
  text-align: center;
  line-height: 75px;
  margin-left: auto;
  margin-right: auto;
  font-size: 30px;
}